import React from "react";

import "./PageDeroulement.css"

function PageFAQ(props) {
    return(
        <React.Fragment>
            <div className="page-accueil">
                <div id="drawer-hook"></div>
                <h1 className="page-accueil_titre">Foire aux questions - FAQ</h1>
                <div className="page-accueil_text">

                
                <div className="page-accueil_soustitre">{">"}Est-ce que le stage est obligatoire?</div>
				 
			   Le stage de fin d'tudes en informatique est obligatoire 
			  pour l'obtention du diplme collggial. 
              <div className="page-accueil_soustitre">{">"}Quel est l'horaire de l'tudiant durant les stages?</div>
				 
			   L'tudiant doit respecter l'horaire de l'entreprise durant son stage. 
			   <div className="page-accueil_soustitre">{">"}Est-ce que l'tudiant travaille pendant les journes pdagogiques et
			  les journes de rattrapage?</div>
				 
			   L'tudiant doit respecter l'horaire de l'entreprise durant son stage et ce mme
			  durant les journes pdagogiques et de rattrapage. 
              <div className="page-accueil_soustitre">{">"}Quelle est la dure d'un stage de fin d'tudes?</div>
				 
			   La dure du stage est de 15 semaines pour les deux profils de sortie (rseaux et programmation). 
			   <div className="page-accueil_soustitre">{">"}Quelles sont les dates prvues pour les stages?</div>
				 
			   Les stages sont prvus du 21 janvier au 3 mai 2019. 
               </div>
            </div>
        </React.Fragment>
    );
}

export default PageFAQ;